import * as constants from "./constants";
import axios from 'axios';
import { fromJS } from 'immutable';
//applying thunk

export const sendTitleAction = (title) => ({
	type: constants.SEND_TITLE,
	title
});

export const loadListAction = (page) => {
	return (dispatch) => {
		dispatch(setLoadingAction(true));
		axios.get(`api/articles${page}.json`)
		.then((res) => {
			const data = res.data.data;
			/*这里进行骨架屏的测试，所以做了一个延迟：300ms*/
			dispatch(setArticleListAction(data.articleList));
			setTimeout(()=>{dispatch(setLoadingAction(false))}, 0);
		})
		.catch(()=>{ 
			dispatch(setFailedAction(true));
		})
	}
}

const setArticleListAction = (list) => ({
	type: constants.SET_LIST,
	list
})

const setLoadingAction = (bool) => ({
	type: constants.SET_LOADING,
	bool
})

const setFailedAction = (bool) => ({
	type: constants.SET_FAILED,
	bool
})