import * as constants from './constants';
import { fromJS } from 'immutable';

const defaultState = fromJS({
	/* 文章数据 */
	list: [],
	/* 加载状态 */
	loading: true,
	failed: false,
	title: ''
});

export default ( state = defaultState, action ) => {
	switch (action.type) {
		case constants.SET_LOADING:
			return state.set("loading", action.bool);
		case constants.SEND_TITLE:
			return state.set("title", action.title);
		case constants.SET_FAILED:
			return state.set("failed", action.bool);
		case constants.SET_LIST:
			/* 
			这里我希望在获取到数据的同时更新loading状态，
			但却发现在这之前loading神奇的变成了undefined ,
			因此没有把异步过程放在这里
			*/
			return state.merge({
				list: action.list
			})
		default:
			return state;
	}
}