import React, { Suspense, lazy, Fragment, useEffect } from 'react';
import {
	Gallery,
	HomeWrapper,
	HomeLeft,
} from './style';
//import NewList from './components/NewList';

const GetTop = lazy(()=>import('./components/GetTop'));
const NewList = lazy(()=>import('./components/NewList'));

//使用Hook进行了重构
const Home = (props) => {

	useEffect(()=>{
		window.scrollTo(0,0);
	})

	return (
		<Fragment>
			<Suspense fallback={<div></div>}>
				<Gallery>
					<div className="logo">
						<div className="banner">
							<h1>Cyan</h1>
							<h2>zone</h2>
						</div>
					</div>
				</Gallery>
				<HomeWrapper>
					<HomeLeft>
							<NewList/>
					</HomeLeft>
						<GetTop/>
				</HomeWrapper>
			</Suspense>
		</Fragment>
	)
}

export default Home;