//根组件
import React, { Suspense, lazy } from 'react';
import { Provider } from 'react-redux';
import { 
  BrowserRouter,
  Route, 
} from 'react-router-dom'; //Router v4
import store from './store';
import { Spin } from 'antd';
import { Loading } from './style.js';
import Home from './pages/home';

//懒加载
const Header = lazy(()=>import('./common/header'));
const Detail = lazy(()=>import('./pages/detail'));
const Login = lazy(()=>import('./pages/login'));
const Register = lazy(()=>import('./pages/register'));
const Footer = lazy(()=>import('./common/footer'));

// import Header from './common/header';
// import Detail from './pages/detail';
// import Login from './pages/login';
// import Register from './pages/register';
// import Footer from './common/footer';

//大部分组件均是class组件
const App = () => {
  return (
    <Provider store={store}>
      <BrowserRouter basename="/cyanblog">
        <Suspense fallback={<Loading><Spin size="large" /></Loading>}>
          <Route path={["/detail/:id", "/"]} exact component={Header}/>
          <Route path='/' exact component={Home}/>
          <Route path='/detail/:id' component={Detail}/>
          <Route path='/login' component={Login}/>
          <Route path='/register' component={Register}/>
          <Route path={["/detail/:id", "/"]} exact component={Footer}/>
        </Suspense>
      </BrowserRouter>
    </Provider>
  );
}

export default App;
