import styled from 'styled-components';
import { Skeleton as Skele } from 'antd';

export const HomeWrapper = styled.div`
    margin: 0 auto;
    margin-top: 20px;
    margin-bottom: 40px;
    width: 1000px;
    overflow: hidden;
    min-height: 100vh;   /*保证底部在最下*/
    background: #fff;
    display: flex;
    flex-wrap: wrap;
    border-radius: 10px;
    justify-content: flex-start;
    padding: 20px;
`;

export const Gallery = styled.div`
    width: 100%;
    height: 200px;
    background: #20232a;
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    align-items: center;
    .banner {
        width: 1000px;
        color: #00BFFF;
        font-weight: 500;
        letter-spacing: 10px;
        h1 {
            line-height: 60px;
            font-size: 60px;
            color: #00BFFF;
        }
        h2 {
            line-height: 60px;
            font-size: 40px;
            color: #00FFFF;
            margin-left: 10px;
        }
    }
`

export const HomeLeft = styled.div`
    width: 700px;
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-start;
`;

export const HomeRight = styled.div`
    width: 280px;
`;

export const TopicWrapper = styled.div`
    padding: 20px 0 10px 0;
    overflow: hidden;
    margin-left: -18px;
`;

export const TopicItem = styled.div`
    background: #F7F7F7;
    height: 32px;
    line-height: 32px;
    font-size: 14px;
    color: #555;
    cursor: pointer;
    margin-left: 18px;
    margin-bottom: 18px;
    border: 1px solid #dcdcdc;
    border-radius: 4px;
    padding-right: 10px;
    &.more {              //更多热门专题
        background: #fff;
        border: none;
        a {
            color: #777;
            text-align: center;
        }
    }
    .item_img {
        float: left;
        width: 32px;
        height: 32px;
        margin-right: 10px;
    }
    &:hover {
        background: #fff;
    }
`;

export const ListItem = styled.div`
    width: 700px;
    margin-bottom: 40px;
    margin-top: 5px;
    display: flex;
    flex-wrap: wrap;
    align-items: flex-start;
    border-bottom: 1px solid rgba(0,0,0,0.1);
    transition: all 100ms ease-in;
    a {
        font-size: 20px;
        font-weight: 600;
        color: #515151;
    }
    a:hover {
        color:#1E90FF;
    }
    .title {
        width: 700px;
    }
    .info {
        width: 700px;
        font-size: 14px;
        margin-top: 20px;
        margin-bottom: 20px;
        display: flex;
        div {
            margin-right: 20px;
        }
    }
    .desc {
        width: 700px;
        font-size: 16px;
        min-height: 150px;
        line-height: 27px;
    }
    .footer {
        width: 700px;
        font-size: 13px;
        line-height: 18px;
        margin-top: 10px;
        margin-bottom: 10px;
    }
`;

export const Bottom = styled.div`
    overflow: hidden;
    line-height: 20px;
    color: rgb(180,180,180);
    font-size: 12px;
    .author {
        float: left;
        margin-right: 10px;
    }
    .comment {
        float: left;
        margin-right: 10px;
    }
    .star {
        float: left;
        margin-right: 10px;
    }
    .iconfont {
        font-size: 13px;
        line-height: 20px;
    }
`;

export const Banner = styled.img`
    width: 280px;
    height: 40px;
    margin-top: 5px;
    margin-bottom: 11px;
    border-radius: 10px;
    cursor: pointer;
    opacity: 0.8;
    transition: all 100ms ease-in;
    &:hover{
        opacity: 1;
        transform: scale(1.02);
    }
`;

export const WriterWrapper = styled.div`
    width: 280px;
`;

export const WriterHeader = styled.div`
    width: 280px;
    overflow: hidden;
    font-size: 14px;
    color: #969696;
    margin-bottom: 20px;
    .rec {
        float: left;
    }
    .switch {
        float: right;
    }
    .iconfont {
        font-size: 14px;
        margin-right: 5px;
    }
`;

export const WriterItem = styled.div`
    width: 280px;
    height: 48px;
    overflow: hidden;
    .photo {
        float: left;
        height: 48px;
        width: 48px;
        border-radius: 24px;
        margin-right: 10px;
    }
    margin-bottom: 15px;
`;

export const WriterInfo = styled.div`
    float: left;
    box-sizing: border-box;
    width: 222px;
    height: 48px;
    padding: 10px 0;
    .header {
        overflow: hidden;
    }
    .name {
        float: left;
        font-size: 14px;
        color: #333;
    }
    .add {
        margin-top:5px;
        float: right;
        font-size: 13px;
        color: #42C02E;
    }
    .info {
        display: block;
        margin-top: 5px;
        font-size: 12px;
        color: #969696;
    }
`;

export const LoadMore = styled.div`
    width: 580px;
    height: 35px;
    margin: 0 auto;
    background: #A5A5A5;
    border-radius: 5px;
    font-size: 15px;
    line-height: 20px;
    color: #FFF;
    text-align: center;
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all 100ms ease-in;
    &:hover {
        background:	#696969;
        color: #FFF;
    }
`;

export const GetTopWrapper = styled.div`
    width: 60px;
    height:60px;
    border: 1px solid rgb(220, 220, 220);
    position: fixed;
    right: 200px;
    bottom: 200px;
    box-sizing: border-box;
    cursor: pointer;
    background: #fff;
    display: flex;
    justify-content: center;
    align-items: center;
    transition: all 200ms ease-in;
    border-radius: 5px;
    color: rgb(51,51,51);
    .iconfont{
        font-size:25px;
    }
    &:hover {
        background:	#696969;
        color: #00FFFF;
    }
`;

export const Skeleton = styled(Skele)`
    width: 100%;
    transition: all 100ms ease-in;
    background: #fff;
`;