import * as actionTypes from "./actionTypes";
import axios from 'axios';
import { fromJS } from 'immutable';
//applying thunk

export const getDetailDataAction = (id) => {
	return (dispatch) => {
		axios.get(`https://cyanzll.github.io/cyanblog/api/md/${id}.md`).then((res)=>{
			const data = res.data;
			dispatch(getMarkdown(data));
			setTimeout(()=>{dispatch(setLoadingAction(false))}, 200);
		});	
	}
};

export const getInDetailAction = (istrue) => ({
	type: actionTypes.GET_IN_DETAIL,
	data: istrue
});

export const setLoadingAction = (bool) => ({
	type: actionTypes.SET_LOADING,
	bool
})

const getMarkdown = (data) => ({
	type: actionTypes.GET_MARKDOWN,
	data
})